export async function POST(req) {
  try {
    const { searchParams } = new URL(req.url);
    const nombre = searchParams.get("nombre") || `Maestro departamentos ${new Date().toISOString().slice(0, 10)}`;

    const auth = new google.auth.OAuth2(
      process.env.GOOGLE_CLIENT_ID,
      process.env.GOOGLE_CLIENT_SECRET
    );

    auth.setCredentials({
      refresh_token: process.env.GOOGLE_REFRESH_TOKEN,
    });

    const sheets = google.sheets({ version: "v4", auth });

    // Crear el spreadsheet
    const createRes = await sheets.spreadsheets.create({
      requestBody: {
        properties: { title: nombre },
      },
    });

    const spreadsheetId = createRes.data.spreadsheetId;
    const spreadsheetUrl = `https://docs.google.com/spreadsheets/d/${spreadsheetId}`;

    // Armar los datos: fila 1 header, cada fila siguiente un departamento
    const values = [["Departamento"], ...DEPARTAMENTOS.map((dep) => [dep])];

    await sheets.spreadsheets.values.update({
      spreadsheetId,
      range: `A1:A${values.length}`,
      valueInputOption: "USER_ENTERED",
      requestBody: { values },
    });

    return Response.json({ ok: true, spreadsheetId, spreadsheetUrl, filas: values.length - 1 });
  } catch (err) {
    console.error("Error creando maestro:", err);
    return Response.json({ error: "No se pudo crear el maestro", details: err.message }, { status: 500 });
  }
}
